.page {
  scroll-behavior: smooth;

  .container {
    max-width: 1200px;
    margin: 0 auto;
  }

  &__body {
    margin: 0 auto;
    background-color: $c-gray-light;
    font-family: "Montserrat", sans-serif;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    color: #333;

  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;

    transform: translateX(100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }
}
